*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

@font-face {
  font-family: DM Sans;
  src: local(DM Sans), url("DMSans-Regular.23c41658.ttf") format("truetype");
  font-weight: 400;
}

@font-face {
  font-family: DM Sans;
  src: local(DM Sans), url("DMSans-Medium.0957dae6.ttf") format("truetype");
  font-weight: 500;
}

@font-face {
  font-family: DM Sans;
  src: local(DM Sans), url("DMSans-Bold.51beb29d.ttf") format("truetype");
  font-weight: 700;
}

@font-face {
  font-family: Titan One;
  src: local(Titan One), url("TitanOne-Regular.2147ecec.ttf") format("truetype");
  font-weight: 400;
}

:root {
  --text-color-primary: #000;
  --text-color-secondary: #fff;
  --text-color-other: #907e82;
  --text-color-titles: #d41443;
  --text-color-labels: #ffa5ba;
  --background-color-primary: #fff;
  --background-color-secondary: #fff5f6;
  --background-color-other: #ffb8ca;
  --background-menu-container: rgba(255, 184, 202, .6);
  --background-mobile-menu: #e17992;
  --streak-color-mobile-menu: #e18298;
  --decorative-color: #ffa5ba;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  color: var(--text-color-primary);
  background-color: var(--background-color-primary);
  margin: 0;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
}

body.modal-open {
  overflow: hidden;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.list {
  padding: 0;
  list-style: none;
}

.link {
  font-style: normal;
  text-decoration: none;
}

.title {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--text-color-titles);
  font-family: Titan One;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.15;
}

.label {
  text-align: center;
  text-transform: uppercase;
  color: var(--text-color-labels);
  font-family: Titan One;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.17;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p, ul {
