.mobile-menu-button {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: inline-flex;
}

.mobile-menu-button__close {
  position: absolute;
  top: 20px;
  right: 20px;
}

.mobile-menu-button__icon-open {
  width: 30px;
  height: 18px;
}

.mobile-menu-button__icon-close {
  width: 21px;
  height: 21px;
}

@media screen and (min-width: 1200px) {
  .mobile-menu-button {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .mobile-menu-button.js-open-menu {
    margin-right: 32px;
  }
}

@media screen and (max-width: 767px) {
  .menu-container {
    z-index: 5;
    width: 100%;
    height: 100%;
    background-color: var(--background-menu-container);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition-property: opacity, visibility;
    transition-duration: .25s;
    transition-timing-function: var(--timing-function);
    position: fixed;
    top: 0;
    left: 0;
    overflow-y: scroll;
  }

  .menu-container.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: initial;
  }
}

@media screen and (min-width: 1200px) {
  .menu-container {
    margin-right: 141px;
  }
}

@media screen and (max-width: 1199px) {
  .menu-content {
    z-index: 6;
    width: 250px;
    height: 100vh;
    background-color: var(--background-mobile-menu);
    transition-property: transform;
    transition-duration: .25s;
    transition-timing-function: var(--timing-function);
    padding: 60px 20px;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(100%);
  }
}

@media screen and (max-width: 1199px) and (min-width: 768px) {
  .menu-content {
    width: 254px;
  }
}

.menu-container.is-open .menu-content {
  transform: translateX(0);
}

